import * as THREE from 'three'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";


import emptyWharehouse from '../resources/empty_warehouse_01_2k.hdr'

import avaxlogo from "../../../images/avaxlogo.png"

export const hdrEquirect = new RGBELoader().load(
  emptyWharehouse,
  () => {
    hdrEquirect.mapping = THREE.EquirectangularReflectionMapping;
  }
);

export function getLight1(){
    const color = "#FFFFFF";
    const intensity = 1;
    const light = new THREE.DirectionalLight(color, intensity);
    light.position.set(-1, 2, 4);
    return light
  }

export function getWater(scene,uBigWavesElevation,waterFragmentShader,waterVertexShader){
   /**
     * Water
     */
    // Geometry
    const waterGeometry = new THREE.PlaneGeometry(2, 2, 512, 512)
    // Material
    const waterMaterial = new THREE.ShaderMaterial({
        vertexShader: waterVertexShader,
        fragmentShader: waterFragmentShader,
        uniforms:
        {
            uBigWavesElevation: { value:  uBigWavesElevation},
            uBigWavesFrequency: { value: new THREE.Vector2(1.826,0.743) },
            uTime: { value: 0 },
            uBigWavesSpeed: { value: 0.75 },
            uDepthColor: { value: new THREE.Color('#517acd') },
            uSurfaceColor: { value: new THREE.Color('#c9cfe8') },
            uColorOffset: { value: 0.028 },
            uColorMultiplier: { value: 3.45 },
            uSmallWavesElevation: { value: 0.105 },
            uSmallWavesFrequency: { value: 3 },
            uSmallWavesSpeed: { value: 0.1 },
            uSmallIterations: { value: 4 },
            fogColor:    { type: "c", value: scene.fog.color },
            fogNear:     { type: "f", value: scene.fog.near },
            fogFar:      { type: "f", value: scene.fog.far },
        } ,
        fog: true
    })

    // Mesh

    const water = new THREE.Mesh(waterGeometry, waterMaterial)
    water.rotation.x = - Math.PI * 0.5
    water.scale.x = 8
    water.scale.y = 8
    return water
  }

export function  getRenderer(sizes){
    const renderer = new THREE.WebGLRenderer({ antialias: true,alpha: true })
    renderer.setClearColor( 0x000000, 0 );
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    return renderer
  }

export function  getEffectComposer(renderer,sizes,scene2,camera2){
    const renderTarget2 = new THREE.WebGLRenderTarget(
      800,
      600,
      {
          minFilter: THREE.LinearFilter,
          magFilter: THREE.LinearFilter,
          format: THREE.RGBAFormat
      }
    )
    const effectComposer = new EffectComposer(renderer,renderTarget2)
    effectComposer.setSize(sizes.width, sizes.height)
    effectComposer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    const renderPass = new RenderPass(scene2, camera2)
    effectComposer.addPass(renderPass)
    return effectComposer
  }  

export function  getRounderdCornersGeometry(){
    const w = 9;	// width
    const h =  16;	// height
    const r = 0.5;	// radius corner
    const s = 18;	// smoothness

    // helper const's
    const wi = w / 2 - r;
    const hi = h / 2 - r;
    const w2 = w / 2;
    const h2 = h / 2;
    const ul = r / w;
    const ur = ( w - r ) / w;
    const vl = r / h;
    const vh = ( h - r ) / h;

    let positions = [

      -wi, -h2, 0,  wi, -h2, 0,  wi, h2, 0,
      -wi, -h2, 0,  wi,  h2, 0, -wi, h2, 0,	
      -w2, -hi, 0, -wi, -hi, 0, -wi, hi, 0,
      -w2, -hi, 0, -wi,  hi, 0, -w2, hi, 0,	
      wi, -hi, 0,  w2, -hi, 0,  w2, hi, 0,
      wi, -hi, 0,  w2,  hi, 0,  wi, hi, 0
      
    ];

    let uvs = [
      
      ul,  0, ur,  0, ur,  1,
      ul,  0, ur,  1, ul,  1,
      0, vl, ul, vl, ul, vh,
      0, vl, ul, vh,  0, vh,
      ur, vl,  1, vl,  1, vh,
      ur, vl,  1, vh,	ur, vh 
      
    ];

    let phia = 0; 
    let phib, xc, yc, uc, vc;

    for ( let i = 0; i < s * 4; i ++ ) {

      phib = Math.PI * 2 * ( i + 1 ) / ( 4 * s );
      
      
      xc = i < s || i >= 3 * s ? wi : - wi;
      yc = i < 2 * s ? hi : -hi;

      positions.push( xc, yc, 0, xc + r * Math.cos( phia ), yc + r * Math.sin( phia ), 0,  xc + r * Math.cos( phib ), yc + r * Math.sin( phib ), 0 );
      
      uc = xc = i < s || i >= 3 * s ? ur : ul;
      vc = i < 2 * s ? vh : vl;
      
      uvs.push( uc, vc, uc + ul * Math.cos( phia ), vc + vl * Math.sin( phia ), uc + ul * Math.cos( phib ), vc + vl * Math.sin( phib ) );
      
      phia = phib;
        
    }
    const geometry = new THREE.BufferGeometry( );
    geometry.setAttribute( 'position', new THREE.BufferAttribute( new Float32Array( positions ), 3 ) );
    geometry.setAttribute( 'uv', new THREE.BufferAttribute( new Float32Array( uvs ), 2 ) );
    return geometry
  }

export function  getPlaneRoundedCorners(renderTarget){
    const material = new THREE.MeshBasicMaterial({
        map: renderTarget.texture
    })

    const geometry = getRounderdCornersGeometry()

    const mesh  = new THREE.Mesh(geometry, material)
    mesh.material.side = THREE.DoubleSide
    mesh.receiveShadow = false
    
    // mesh.rotation.x = -Math.PI / 2
    return mesh
  }

export function  getPlaneRoundedCornersTransparent(){
    const material = new THREE.MeshPhysicalMaterial({
      transmission: 1,
      thickness: 3,
      roughness:0,
      envMapIntensity:1.5,
      envMap: hdrEquirect
    });

    // const geometry =  new THREE.IcosahedronGeometry(0.75, 0)
    let width =10, height =16, depth =0 , radius0 =1, smoothness = 20
    let shape = new THREE.Shape();
    let eps = 0.00001;
    let radius = radius0 - eps;
    shape.absarc(eps, eps, eps, -Math.PI / 2, -Math.PI, true);
    shape.absarc(eps, height - radius * 2, eps, Math.PI, Math.PI / 2, true);
    shape.absarc(width - radius * 2, height - radius * 2, eps, Math.PI / 2, 0, true);
    shape.absarc(width - radius * 2, eps, eps, 0, -Math.PI / 2, true);
    let geometry = new THREE.ExtrudeBufferGeometry(shape, {
      //  amount: depth - radius0 * 0.2,
        amount:0.01,
       bevelEnabled: true,
       bevelSegments: smoothness * 2,
       steps: 1,
       bevelSize: radius,
       bevelThickness: radius0,
       curveSegments: smoothness
    });
 
    geometry.center();
    
    const mesh  = new THREE.Mesh(geometry, material)
    mesh.material.side = THREE.DoubleSide
    // mesh.receiveShadow = false
    
    // mesh.rotation.x = -Math.PI / 2
    return mesh
  }

  
export function  getControls1(camera, canvas){
    const controls = new OrbitControls(camera, canvas)
    controls.enableDamping = true
    controls.minPolarAngle=Math.PI / 5
    controls.maxPolarAngle=Math.PI / 2.5
    controls.minDistance = 1;
    controls.maxDistance = 2;
    controls.rotateSpeed =1
    controls.enablePan = false
    controls.autoRotate = true
    return controls
  }

  export function  getControls2(camera2, canvas){
    const controls2 = new OrbitControls(camera2, canvas)
    controls2.enableDamping = true
    // controls2.rotateSpeed =0.8
    controls2.maxDistance = 19
    controls2.minDistance = 17
    controls2.minPolarAngle =1.19
		controls2.maxPolarAngle =  Math.PI * 0.6
    controls2.enablePan = false
    return controls2
  }

export function  getCamera1(sizes){
    const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
    camera.position.set(1, 0.01, 1)
    return camera
  }

export function  getCamera2(sizes){
    const camera2 = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
    camera2.position.set(0, 0, 17)
    return camera2
  }

export function  getScene1(){
    const scene = new THREE.Scene()
    const near = 0.1;
    const far = 5;
    const color = '#FFFFFF';
    scene.fog = new THREE.Fog(color, near, far)
    scene.background = new THREE.Color(color)
    return scene
  }

export function getAVAXLogo(){
    let texture = new THREE.TextureLoader().load(avaxlogo);
    let flwmtl= new THREE.MeshBasicMaterial( { map: texture ,opacity: 1, transparent: true } )
    console.log("@@@ texture",texture)
    let gflw= new THREE.PlaneGeometry(11, 11, 512, 512)
    let logo = new THREE.Mesh(gflw,flwmtl)
    logo.material.side = THREE.DoubleSide

    return logo
}

export function getTrippyMesh(trippyVertexShader,trippyFragmentShader){
  const geometry = new THREE.PlaneGeometry(2, 2, 512, 512)

  // Material
  const material = new THREE.ShaderMaterial({
      vertexShader: trippyVertexShader,
      fragmentShader: trippyFragmentShader,
      side: THREE.DoubleSide,
      uniforms:
      {
          uTime: { value: 0 },
      }
  })
  
  // Mesh
  const mesh = new THREE.Mesh(geometry, material)
  mesh.rotation.x = - Math.PI * 1.5
  mesh.scale.x = 8
  mesh.scale.y = 8
  return mesh
}