import React from "react";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';


class Header extends React.Component {

    render() {
        return (
            <Container  >
            <Grid container >
                <Grid item md={2} xs={6}>
                    <div>
                        <h1 >
                        Header
                        </h1>
                    </div>
                </Grid>
                <Grid item md={9} xs={false}>
                   
                </Grid>
                <Grid item md={1} xs={6}>
                    <div>
                        <h1 >
                        Wallet
                        </h1>
                    </div>
                </Grid>
            </Grid>
        </Container>
        );
    }
}

export default Header;