import React from "react";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

import Header from "../components/Header"

import TrippyElement1 from "../components/three/TrippyElement1/TrippyElement1"
import TrippyElement2 from "../components/three/TrippyElement2/TrippyElement2"
import WaterElement from "../components/three/WaterElement/WaterElement"


class HomePage extends React.Component {

    render() {
      return (
        <>
        <Header />
        <Container  maxWidth="sm">
          <Grid container >
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                    <TrippyElement1  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement2  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <WaterElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
           </Grid>
        </Container>
        </>
      );
    }
  }
export default HomePage;